<!--  -->
<template>
  <div>
    <div v-for="(child , key) in tree" :key="key">
      <span
        @click="scroll(key)"
        :id="'title-'+key"
        :style="'padding-left:'+lev+'rem'"
      >{{ child.tag }}{{ child.title }}</span>
      <CatalogTreeItem :item="child " v-if="child.children" :rank="lev" />
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
// import CatalogTreeItem from "./CatalogTreeItem";
export default {
  name: "CatalogTree",
  props: {
    tree: Array,
    rank: Number
  },
  //import
  components: {
    // CatalogTreeItem
  },
  data() {
    //
    return {
      lev: Number
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    scroll(index) {
      // window.scroll(0, this.tree[index].scrollTop);
      window.scrollTo({
        top: this.tree[index].scrollTop,
        behavior: "smooth"
      });
      console.log(this.tree[index].scrollTop)
      // console.log(document.getElementById('title-'+index).offsetTop   )
      // console.log(this.tree[index].scrollTop)
      // console.log(this.tree[index+1].scrollTop)
      // console.log(document.documentElement.scrollTop-100)
      // console.log(this.$route)
    },
    level() {
      this.lev = this.rank;
      this.lev++;
    }
  },
  // - this
  created() {
    this.level();
  },
  // - DOM
  mounted() {},
  beforeCreate() {
    this.$options.components.CatalogTreeItem = () =>
      import("./CatalogTreeItem");
  }, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style  scoped>
span {
  display: inline-block;
  width: 100%;
  margin: 0.4rem 0;
  padding: 0 2rem;
  height: 3rem;
  line-height: 3rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: bold;
  transition: 0.2s;
  user-select: none;
}
span:hover {
  background: rgba(50, 109, 255, 1);
  font-weight: bolder;
}
</style>