<!--  -->
<template>
  <nav>
    <div class="container">
      <router-link tag='div' to="/" class="logo">BLOG</router-link>
      <ul v-show="isShow">
        <router-link to="/" tag="li"></router-link>
        <router-link to="/about" tag="li"></router-link>
        <router-link to="/archieve" tag="li"></router-link>
        <router-link to="/todo" tag="li">TODO</router-link>
      </ul>
      <div class="btn"  @click.stop="didClickMenus">More</div>
    </div>
  </nav>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  data() {
    //
    return {
      isShow:true,
      screenWidth: document.body.clientWidth
    };
  },
  // data
  computed: {},
  //data
  watch: {
    screenWidth(val) {
      this.screenWidth = val;
      if(val > 1083){
        this.isShow = true
      }else{
        this.isShow = false
      }
    }
  },
  //
  methods: {
    didClickMenus(){
      this.isShow=!this.isShow
    }
  },
  // - this
  created() {},
  // - DOM
  mounted() {
    const that = this;
    window.onresize = () => {
      return (() => {
        // window.screenWidth = document.body.clientWidth;
        // that.screenWidth = window.screenWidth;
        that.screenWidth = document.body.clientWidth
      })();
    };
    document.onclick = () => {
      if (this.isShow && this.screenWidth<1083) {
        this.isShow = false;
      }
    };
    if(this.screenWidth < 1083){
      this.isShow = false
    }
  },
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style  scoped>
nav {
  background: rgba(0, 255, 196, 0);
  height: 5rem;
  line-height: 5rem;
  width: 100%;
  font-size: 1.2rem;
  font-weight: 900;
  color: #ffffff;
  position: fixed;
  top: 0;
  transition: 0.9s;
  z-index: 3;
}

.logo {
  float: left;
  font-size: 2rem;
}

@media screen and (max-width: 1100px) {
  nav{
    background: rgba(255, 134, 50, 0.8);
  }
  ul {
    width: 12rem;
    position: absolute;
    right: 2rem;
    top: 5rem;
    background: white;
    color: gray;
     box-shadow: 0.5rem 0.5rem 1.8rem #888;
  }
  li {
    width: 100%;
    text-align: center;
    margin: 0 0;
    font-size: 2rem;
  }
  .logo{
    font-size:2.4rem;
  }
}
@media screen and (min-width: 1100px) {
  .btn {
    display: none;
  }
  ul {
    float: right;
  }
  li {
    float: left;
    margin: 0 2rem;
    font-size: 1.4rem;
  }
}

li:hover {
  color: gray;
}
.btn {
  float: right;
  font-size: 2.4rem;
}
</style>