<template>
  <div id="header" ref="change">
    <Nav />

    <div class="container" ref="bg">
      <div class="title">Zhangxc Blog</div>
      <div class="sub-title">
        <div id="word-box"></div>
      </div>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import Nav from "./Nav";
import Input from "../assets/animate/word";
export default {
  //import
  components: { Nav },
  data() {
    //
    return {};
  },
  // data
  computed: {},
  //data
  watch: {
    $route(to, from) {
      // console.log(to.path);
      // let ele = document.getElementById("header");
      let ele = this.$refs.change;
      let bg = this.$refs.bg;
      // console.log(to)
      if (document.documentElement.offsetWidth > 1080) {
        if (to.name === "article") {
          ele.style.height = "20rem";
          bg.style.display = "none";
        } else {
          ele.style.height = "34rem";
          bg.style.display = "block";
        }
      }
    }
  },
  //
  methods: {},
  // - this
  created() {},
  // - DOM
  mounted() {
    //
    Input.input(" ", "Hello World");
  },
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style  scoped>
#header {
  position: relative;
  /* top: -5rem; */
  background-image: url("../assets/home-bg.jpg");
  height: 34rem;
  width: 100%;
  background-color: #fff;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-background-size: cover;
  -o-background-size: cover;
  background-position: center 60%;
  transition: 0.5s;
  background-size: 100%;
  user-select: none;
  transition: background 3s;
  transition-duration:ease-in-out
}

#header:hover {
  background-size: 120%;
  /* background-position: center 100%; */
}
@media screen and (max-width: 799px) {
  #header {
    height: 20rem;
    background-size: 120%;
  }
}
@media screen and (max-width: 370px) {
  #header {
    height: 20rem;
    background-size: 140%;
  }
}
.container {
  padding-top: 5rem;
}

.title {
  color: #fff;
  font-size: 6rem;
  text-align: center;
  font-weight: bolder;
}
.sub-title {
  color: #fff;
  font-size: 2rem;
  text-align: center;
}
</style>