<!--  -->
<template>
  <transition name="box">
    <div id="toast" v-if="alive">{{msg}}</div>
  </transition>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  name: "toast",
  components: {},
  props:{
    msg:String
  },
  data() {
    //
    return {
      alive: false,
      timer: null
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    destroyToast() {
      this.alive = true;
      if (this.timer) {
        clearTimeOut(this.timer);
      } else {
        this.timer = setTimeout(() => {
          document.getElementById("toast").style.opacity = 0;
          this.alive = false;
        }, 2000);
      }
    }
  },
  // - this
  created() {},
  // - DOM
  mounted() {
    this.destroyToast();
  },
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style  scoped>
#toast {
  position: fixed;
  top: 6%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: wheat;
  text-align: center;
  height: 6rem;
  line-height: 6rem;
  width: 16rem;
  user-select: none;
  transition: 1s;
  opacity: 1;
  font-size: 2rem;
  font-weight: bolder;
  border-radius: 4rem
}
.box-enter,
.box-leave-to {
  opacity: 0;
}
.box-enter-to,
.box-leave {
  opacity: 1;
}
.box-enter-active,
.box-leave-active {
  transition: all 1s;
}
</style>