<!--  -->
<template>
  <div class="body">
    <ArticleCard v-for="(value,key) in articles" :key="key" :articles="articles" :index="key" />
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import ArticleCard from "../components/ArticleCard";
export default {
  //import
  components: { ArticleCard },
  props: {
    id: {}
  },
  data() {
    //
    return {
      articles: [
        {
          tags: [],
          title: "",
          subTitle: "",
          description: "",
          context: "",
          createTime: "",
          lastEditTime: "",
          _id: ""
        }
      ]
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    async fetchArticles() {
      if (this.id) {
        const res = await this.$http.get(`tags/${this.id}`);
        this.articles = res.data[0].articlesList;
        console.log(res.data[0].articlesList)
      } else {
        const res = await this.$http.get("articles/list");
        this.articles = res.data;
        this.articles.reverse()
      }
      this.$nextTick(function(){
        if(!this.articles[0]){
          this.$TOAST('')
        }
      })
    }
  },
  // - this
  created() {
    this.fetchArticles();
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style  scoped>
.body {
  width: 92%;
  margin: 0 4%;
  padding: 0.1rem 0.1rem;
  box-shadow: 0.5rem 0.5rem 1.8rem #888;
  user-select:none;
}
@media screen and (max-width: 768px) {
  .body {
    width: 96%;
  margin: 0 2%;
  }
}
</style>