<!--  -->
<template>
  <div class="tag-card-body">
    <div class="title"></div>
    <TagCard v-for="(value,key) in tagList" :key="key" :tagList="tagList" :index="key" />
    <div class="footer line"></div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import TagCard from "../components/TagCard";
export default {
  //import
  components: {
    TagCard
  },
  data() {
    //
    return {
      tagList: [
        {
          name: "",
          num: Number,
          _id: ""
        }
      ]
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    tagSort() {
      this.tagList = this.tagList.sort((l, r) => r.num - l.num);
    },
    async fetchTags() {
      const res = await this.$http.get("tags/list");
      this.tagList = res.data;
      this.tagSort();
    }
  },
  // - this
  created() {
    this.fetchTags();
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style  scoped>
.tag-card-body {
  width: 98%;
  margin: 2rem 1%;
  /* background: blue; */
  padding: 3%;
  box-shadow: 0.5rem 0.5rem 1.8rem #888;
  transform: rotate(5deg)
}

.title {
  margin-bottom: 2rem;
  text-align: center;
  font-size: 1.8rem;
  font-weight: bold;
}
.footer {
  margin-top: 2rem;
  position: relative;
}
</style>