<!--  -->
<template>
  <div class="body">
    <div class="container" :class="this.show?'container':'container-unshow'">
      <div
        class="markdown-title"
        style="padding:4rem 0; margin:0 0 4rem 0; box-shadow: 0.2rem 0.2rem 0.8rem #888;text-align:center "
      >
        <p class="title" style="font-size:4rem;font-weight:bolder">{{model.title}}</p>
        <p style="font-size:3rem">{{model.subTitle}}</p>
        <p style="font-size:2rem">{{model.description}}</p>
        <p style="font-size:2rem">{{model.createTime}}</p>
        <p style="font-size:2rem">{{model.lastEditTime}}</p>
        <div><TagCard v-for="(value,key) in model.tags" :key="key" :tagList="model.tags" :index="key" /></div>
      </div>
      <div class="markdown-body" v-html="model.contentHtml" />
    </div>
    <div class="show-btn" :class="this.show?'show-btn':'show-btn-unshow'" @click="isShow">{{word}}</div>
    <div class="catalog" :class="this.show?'catalog':'unshow'">
      <div class="title"></div>
      <CatalogTree :tree="tree" :rank="rank" />
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import catalog from "../assets/power/catalog";
import CatalogTree from "../components/CatalogTree";
import "../assets/css/markdown.css";
import TagCard from '../components/TagCard'
export default {
  //import
  components: {
    CatalogTree,
    TagCard
  },
  props: {
    id: {}
  },
  data() {
    //
    return {
      model: {
        context: "",
        createTime: "",
        description: "",
        lastEditTime: "",
        subTitle: "",
        title: "",
        contentHtml: "",
        tags:''
      },
      tree: [],
      show: true,
      rank: 0
    };
  },
  // data
  computed: {
    prop() {
      let data = {
        subfield: false, // 
        defaultOpen: "preview", //edit   preview 
        editable: false,
        toolbarsFlag: false,
        scrollStyle: true
      };
      return data;
    },
    word() {
      return this.show ? "close" : "open";
    }
  },
  //data
  watch: {},
  //
  methods: {
    async fetch() {
      const res = await this.$http.get(`articles/${this.id}`);
      this.model = res.data;
      this.$nextTick(function() {
        this.tree = catalog.catalog();
      });
    },
    isShow() {
      this.show = !this.show;
    }
  },
  // - this
  created() {
    this.fetch();
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style  scoped>
.container {
  width: 70%;
  /* margin: 0 15%; */
  margin: 0 0 0 25%;
  padding: 6rem 0;
  transition: 0.2s;
  /*  */
  position: relative;
}
.container-unshow {
  width: 70%;
  margin: 0 15%;
  padding: 6rem 0;
}
.catalog {
  background: rgba(121, 109, 255, 1);
  display: block;
  position: fixed;
  top: 0rem;
  font-size: 1.2rem;
  width: 20%;
  overflow: hidden;
  height: 100%;
  overflow-y: auto;
  color: white;
  /* left: -20%; */
  left: 0;
  transition: 0.2s;
  z-index: 10;
}
.unshow {
  left: -20%;
  /* display: none; */
}
.show-btn {
  position: fixed;
  top: 90%;
  left: 20%;
  height: 4rem;
  background: rgba(1, 1, 1, 0.1);
  z-index: 1;
  transition: 0.2s;
  line-height: 4rem;
  padding: 0 2rem;
  font-size: 1.6rem;
}
.show-btn-unshow {
  left: 0;
}
.title {
  font-size: 1.8rem;
  font-weight: bolder;
  height: 4rem;
  line-height: 4rem;
}
@media screen and (max-width: 768px) {
  .container {
    width: 90%;
    margin: 0 5%;
    padding: 2rem 0;
  }
}
@media screen and (max-width: 799px) {
  /* .markdown-body {
    font-size: 1rem
  } */

  .catalog {
    display: none;
  }
  .show-btn {
    display: none;
  }
}
.catalog::-webkit-scrollbar {
  width: 1.6rem;
  height: 0.6rem;
  background: transparent;
}

.catalog::-webkit-scrollbar-thumb {
  background: transparent;
  border-radius: 0.4rem;
}

.catalog:hover::-webkit-scrollbar-thumb {
  background: hsla(0, 0%, 53%, 0.4);
}

.catalog:hover::-webkit-scrollbar-track {
  background: hsla(0, 0%, 53%, 0.1);
}
</style>