import Vue from "vue";
import Toast from "./Toast.vue";
let Builder = Vue.extend(Toast);
export default {
  install(vue) {
    vue.prototype.$TOAST = this.getComponent;
  },
  getComponent(msg) {
    //
    let instance = new Builder({
      propsData: { msg: msg}
    });
    //dom
    instance.vm = instance.$mount();
    document.body.appendChild(instance.vm.$el);
    return instance;
  }
};
