<template>
  <div class="body">
    <div class="player" @click="isPaused()" title="/">
      
      <audio preload muted loop :src="url" id="media"> audio </audio>
    </div>
    <!-- <div class="msg">{{singer}}=={{song}}</div> -->
    <!--  -->
    <ul id="list" :class="{'show':isShow,'unshow':!isShow}">
      <li
        v-for="(v,k) in musicList"
        :key="k"
        @click="getMusic(musicList[k].id)"
        class="item"
        :class="{'active':cur ===k}"
      >{{musicList[k].song}}{{musicList[k].singer}}</li>
    </ul>
    <div class="volume">
      <div @click="addVolume" class="add"></div>
      {{volume}}%
      <div @click="minusVolume" class="minus"></div>
    </div>
    <div class="btn" @click="isShow?isShow=!isShow:isShow=!isShow">{{word}}</div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
let isRoutate;
export default {
  //import
  components: {},
  data() {
    //
    return {
      isShow: false,
      music: [],
      singer: "",
      song: "",
      url: "",
      deg: 0,
      //true
      paused: false,
      musicList: [
        {
          singer: "",
          song: "",
          id: ""
        }
      ],
      cur: "",
      volume: "100"
    };
  },
  // data
  computed: {
    word() {
      if (this.isShow) {
        return "";
      } else {
        return "";
      }
    }
  },
  //data
  watch: {
    paused(value) {
      if (value) {
        isRoutate = setInterval(this.rotate, 20);
      } else {
        clearInterval(isRoutate);
      }
    }
  },
  //
  methods: {
    async getMusic(id) {
      //get
      const res = await this.$http.get(
        `https://api.imjad.cn/cloudmusic/?type=song&id=` + id + `&br=320000`
      );
      this.music = res.data;
      this.url = this.music.data[0].url;
      //id,
      let index;
      for (let i = 0; i < this.musicList.length; i++) {
        if (this.musicList[i].id === id) {
          index = i;
          break;
        }
      }
      this.singer = this.musicList[index].singer;
      this.song = this.musicList[index].song;
      this.cur = index;
      //?reload():isPaused()
      this.$nextTick(function() {
        this.reload();
        // this.isPaused()
      });
    },

    async fetchList() {
      const res = await this.$http.get("musics/list");
      this.musicList = res.data;
      this.$nextTick(function() {
        this.getMusic(this.musicList[0].id);
      });
    },
    isPaused() {
      let audio = document.getElementById("media");
      if (audio.paused) {
        //
        audio.play();
        this.paused = true;
      } else {
        audio.pause();
        this.paused = false;
      }
    },
    reload() {
      let audio = document.getElementById("media");
      audio.play();
      this.paused = true;
      this.deg = 0;
    },
    rotate() {
      let player = document.getElementsByClassName("player")[0];
      player.style.transform = "rotate(" + this.deg + "deg)";
      this.deg += 1;
      if (this.deg === 360) {
        this.deg = 0;
      }
    },
    addVolume() {
      // this.$TOAST("");
      console.log(document.getElementById("media").volume);
      if (document.getElementById("media").volume < 1) {
        this.volume += 10;
        document.getElementById("media").volume = this.volume / 100;
      } else {
        this.$TOAST("");
      }
    },
    minusVolume() {
      // this.$TOAST("");
      console.log(document.getElementById("media").volume);
      if (document.getElementById("media").volume > 0) {
        this.volume -= 10;
        document.getElementById("media").volume = this.volume / 100;
      } else {
        this.$TOAST("");
      }
    }
  },
  // - this
  created() {
    this.fetchList();
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style  scoped>
.player {
  position: fixed;
  top: 5rem;
  right: 0.4rem;
  width: 6rem;
  height: 6rem;
  background: wheat;
  text-align: center;
  line-height: 6rem;
  font-size: 2rem;
  border-radius: 50%;
  user-select: none;
  box-shadow: 0.2rem 0.2rem 0.8rem #888;
}
.show {
  right: 0;
}
.unshow {
  right: -90%;
}
ul {
  position: fixed;
  top: 20rem;
  right: 0;
  background: wheat;
  user-select: none;
  box-shadow: 0.2rem 0.2rem 0.8rem #888;
  transition: 0.2s;
}
li {
  font-size: 1.2rem;
  line-height: 1.5;
  margin: 1rem;
  font-weight: bolder;
  transition: 0.2s;
}
.btn {
  position: fixed;
  top: 17rem;
  right: 0;
  background: wheat;
  text-align: center;
  font-size: 1.2rem;
  line-height: 1.5;
  padding: 0.7rem 0.8rem;
  user-select: none;
  box-shadow: 0.2rem 0.2rem 0.8rem #888;
}

@media screen and (max-width: 1000px) {
  .body {
    display: none;
  }
}
.msg {
  position: fixed;
  top: 8rem;
  right: 10rem;
  background: white;
  user-select: none;
  box-shadow: 0.2rem 0.2rem 0.8rem #888;
  padding: 0.6rem;
}
.right {
  text-align: right;
}
.item {
  opacity: 0.4;
}
.active {
  opacity: 1;
}
.volume {
  position: fixed;
  top: 12rem;
  right: 0;
  background: wheat;
  user-select: none;
  box-shadow: 0.2rem 0.2rem 0.8rem #888;
  line-height: 4rem;
  height: 4rem;
  text-align: center;
  font-size: 1.2rem;
  font-weight: bolder;
  border-radius: 2rem;
  /* transform: rotate(90deg) */
  opacity: 0.4;
  transition: 1s;
}

.volume:hover {
  opacity: 1;
}

.add {
  font-size: 1.2rem;
  text-align: center;
  display: inline-block;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  line-height: 3rem;
  background: wheat;
  margin: 0 1rem;
}
.minus {
  font-size: 1.2rem;
  font-weight: bolder;
  text-align: center;
  display: inline-block;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  line-height: 3rem;
  background: wheat;
  margin: 0 1rem;
}
</style>