<!--  -->
<template>
  <div id="body">
      <!--  -->
    <mavon-editor
      class="md"
      :value="context"
      :subfield="prop.subfield"
      :defaultOpen="prop.defaultOpen"
      :toolbarsFlag="prop.toolbarsFlag"
      :editable="prop.editable"
      :scrollStyle="prop.scrollStyle"
    ></mavon-editor>
    <!--  -->
    <!-- <mavon-editor v-model="context" :toolbars="toolbars"  /> -->
  </div>
</template>

<script>
//jsjsjson
export default {
  //import
  components: {},
  data() {
    //
    return {
      context:'', //
      toolbars: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        mark: true, // 
        superscript: true, // 
        quote: true, // 
        ol: true, // 
        link: true, // 
        imagelink: true, // 
        help: true, // 
        code: true, // code
        subfield: true, // 
        fullscreen: true, // 
        readmodel: true, // 
        /* 1.3.5 */
        undo: true, // 
        trash: true, // 
        save: true, // eventssave
        /* 1.4.2 */
        navigation: true // 
      },
      articleDetail: {
        context: ``
      } //
    };
  },
  // data
  computed: {
    prop() {
      let data = {
        subfield: false, // 
        defaultOpen: "preview", //edit   preview 
        editable: false,
        toolbarsFlag: false,
        scrollStyle: true
      };
      return data;
    }
  },
  //data
  watch: {},
  //
  methods: {},
  // - this
  created() {},
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style  scoped>
#body {
  width: 92%;
  margin: 0 4%;
  /* height: 100%; */
  /* background: gold; */
}
.md{
  display: block;
  max-width: 100%;
  min-height:25rem;
  min-width:25rem;
  font-size: 2rem;
}
</style>