<!--  -->
<template>
  <div class="card-body">
    <div class="title">{{model[0].name}}</div>
    <div class="avatar">
      <img :src="model[0].avatar" alt />
    </div>
    <div class="word">{{model[0].description}}</div>
    <div class="word">{{model[0].context}}</div>
    <div class="social"></div>
    <div class="footer line"></div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  data() {
    //
    return {
      model: [
        {
          avatar: "",
          name: "",
          description: "",
          context: ""
        }
      ]
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    async fetchCard() {
      const res = await this.$http.get("abouts/list");
      this.model = res.data;
    }
  },
  // - this
  created() {
    this.fetchCard();
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style  scoped>
.card-body {
  width: 98%;
  margin: 5rem 1%;
  /* background: blue; */
  padding: 3%;
  box-shadow: 0.5rem 0.5rem 1.8rem #888;
  transform: rotate(-5deg);
  user-select: none;
}
.card-body > * {
  text-align: center;
}
.title {
  font-size: 1.8rem;
  font-weight: bold;
}
.avatar {
  margin: 4rem auto;
  width: 70%;
  transition: 0.5s;
}

img {
  /* border-radius: 50%; */
  border-radius: 1rem;
  transition: 0.5s;
  transform: rotate(-10deg);
}
.avatar:hover {
  width: 80%;
  margin: 2rem auto;
}
img:hover {
  box-shadow: 0.7rem 0.8rem 1rem #dc5bf0, -1rem -1rem 2rem #c9c91e;
  /* border-radius: 1rem; */
  border-radius: 50%;
  transform: rotate(360deg);
}
.word {
  color: #ccc;
  font-size: 1.4rem;
  margin: 0.5rem 0;
}
.footer {
  margin-top: 2rem;
  position: relative;
}
</style>